// *----------------------------------*\
//  OBJECTS > Container
// *----------------------------------*/
.o-container {
	max-width: 100%;
	@screen exlg {
		max-width: 1199px;
		@apply mx-auto;
	}
	@screen xl {
		max-width: 1308px;
		@apply mx-auto;
	}
}

.o-sm-break-line {
	@apply inline;
	@media (max-width: 768px) {
		@apply block;
	}
}
