// *----------------------------------*\
//  OBJECTS > Overlay
// *----------------------------------*/

.o-overlay {
	@apply relative;

	&::before {
		content: '';
		@apply absolute top-0 left-0 h-full w-full z-10 bg-black opacity-50;
	}
}

.o-full-overlay {
	@apply bg-black;
	opacity: 0;
	visibility: hidden;
	transition: all 0.3s ease-out;

	&.js-active {
		opacity: 0.7;
		visibility: visible;
	}
}
