.c-checkbox {
  @apply relative;
  input {
    @apply absolute inset-0 opacity-0;
    height: 0;
    width: 0;
  }
  label {
    @apply relative pl-32 cursor-pointer;

    &::before {
      content: "";
      @apply block border border-gray-300 cursor-pointer absolute left-0 -top-4;
      height: 25px;
      width: 25px;
    }
  }

  input {
    &:checked {
      + label {
        &::before {
          @apply bg-blue-100 border-blue-100;
          background-image: url("../images/icon-check-white.svg");
          background-repeat: no-repeat;
          background-size: contain;
          background-position: center;
        }
      }
    }
  }

  &.radio {
    label {
      &::before {
        border-radius: 50px;
      }
    }
  }
}
