.slick-dots {
	@apply flex items-center justify-center mt-24;
	li {
		padding: 0 3px;
		button {
			padding: 0;
			height: 3px;
			width: 14px;
			display: block;
			overflow: hidden;
			text-indent: -9999px;
			@apply bg-gray-300;
			border-radius: 10px;
		}
	}

	.slick-active {
		button {
			@apply bg-blue-300;
		}
	}
}
