// *----------------------------------*\
//  ELEMENTS
//  Include styles for barebone HTML elements Styles
// *----------------------------------*/

body {
  @apply font-gotham font-normal text-base overflow-x-hidden;
}
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul {
  @apply mb-0;
}
@media print {
  .hidden-print {
    display: none;
  }
  .print-p-0 {
    padding-top: 0 !important;
    padding-bottom: 0 !important;
  }
}

p {
  @apply mb-20;
}

.table-responsive {
  overflow: auto;
}

.video-wrapper {
  @apply relative;
  background: url("../images/loader.gif") no-repeat center center;
  background-size: 50px;
  @apply bg-gray-100;
  &::before {
    padding-bottom: 56.67%;
    content: "";
    display: block;
  }
  iframe {
    @apply absolute inset-0 h-full w-full;
  }
}

a {
  &:visited,
  &:active {
    color: inherit !important;
  }
}

ul {
  list-style: none;
  padding: 0;
}
