.c-header {
  .menu {
    @screen lg {
      @apply flex;
    }
    li {
      @apply text-gray-400;
      &.current_page_item {
        a {
          &::before {
            max-width: 100%;
          }
        }
      }
      @screen lg {
        @apply text-white;
      }
    }
  }
  a {
    @apply font-bold uppercase text-sm inline-block py-10 relative;
    white-space: nowrap;
    transition: all 0.3s;
    &::before {
      content: "";
      @apply absolute left-0 right-0 bg-gray-400 block;
      top: 50%;
      height: 2px;
      max-width: 0;
      width: 100%;

      transform: translateY(-50%);
      transition: all 0.3s;
    }
  }
  .navbar-trigger {
    @apply cursor-pointer;
    svg {
      transform: rotate(45deg);
      transition: all 0.3s;
    }

    &.active {
      @apply text-gray-400;
      svg {
        transform: rotate(0deg);
        > * {
          @apply fill-black;
        }
      }
      @screen lg {
        svg {
          > * {
            @apply fill-white;
          }
        }
      }
    }
  }
  .navbar-dropdown {
    @apply hidden;

    @screen lg {
      .menu {
        transform: translateX(-10px);
        transition: all 0.3s;
        a {
          &:hover {
            &::before {
              @apply max-w-full;
            }
          }
          @screen lg {
            padding: 0 5px;
            margin-right: 15px;
            @apply text-white;
            &::before {
              @apply bg-white;
            }
          }
        }
      }
      &.active {
        .menu {
          transform: translateX(0);
        }
      }
    }
  }
  .navbar-social {
    a {
      @apply flex ml-16;
      @screen lg {
        @apply ml-20;
      }
    }
    img {
      width: 16px;
      &.icon-type-2 {
        @apply hidden;
      }
    }
  }

  .navbar-overlay {
    background-color: rgba(#000, 0.3);
    display: none;
    &.active {
      display: block;
    }
  }

  .navbar-logo,
  .navbar-social {
    transition: opacity 0.4s;
  }
  &.heading-2 {
    li {
      @apply text-gray-400;
    }
    a {
      @apply text-gray-400;
    }

    .navbar-overlay {
      @screen lg {
        display: none !important;
      }
    }

    .navbar-dropdown {
      .menu {
        a {
          &::before {
            @apply bg-gray-400;
          }
        }
      }
    }

    .navbar-trigger {
      color: #000;
      svg {
        fill: #000;
        > * {
          fill: #000;
        }
      }
    }

    .navbar-social {
      img {
        &.icon-type-1 {
          @apply hidden;
        }
        &.icon-type-2 {
          @apply block;
        }
      }
    }
  }
  .navbar {
    &.active {
      .navbar-logo,
      .navbar-social {
        opacity: 0;
      }
    }
  }
}
