.c-banner {
  @apply relative;
  img {
    @apply object-cover;
  }
  .placeholder {
    height: auto;
    &::before {
      content: "";
      @apply block absolute inset-0 bg-black opacity-50 z-10;
    }

    @screen lg {
      // height: 100vh;
    }
  }
  &.c-banner-home {
    .placeholder {
      height: 100vh;
    }
  }
  .blocks {
    .block {
      @apply relative tracking-widest;
      transition: all 0.4s;
      &::before {
        content: "";
        @apply inset-0 absolute z-10;
        background-color: rgba(#000, 0.5);
        opacity: 0;
        transition: opacity 0.5s ease-in-out;
      }
      .block-text {
        cursor: pointer;
        @apply text-white;
        &:visited,
        &:active {
          color: #fff !important;
        }
        @screen lg {
          padding-bottom: 100px;
          @apply tracking-normal;
          z-index: 99;
          transition: all 0.3s linear;
          &:hover {
            @apply tracking-wide;
          }
        }
      }
      &:first-child {
        @screen lg {
          .block-text {
            position: relative;
            &::before {
              content: "";
              @apply block absolute left-0 bg-white;
              bottom: 150px;
              width: 20%;
              height: 1px;
              transition: all 0.3s ease-in-out;
            }
          }
        }
      }
      &:last-child {
        @screen lg {
          .block-text {
            position: relative;
            &::after {
              content: "";
              @apply block absolute right-0 bg-white;
              bottom: 150px;
              width: 20%;
              height: 1px;
              transition: all 0.3s ease-in-out;
            }
          }
        }
      }

      &:first-child,
      &:last-child {
        @screen lg {
          &:hover {
            .block-text {
              &::before,
              &::after {
                width: 25%;
              }
            }
          }
        }
      }
      &:nth-child(2) {
        border-color: rgba(#fff, 0.3);
        @apply border-l-0 border-r-0;
        .block-text {
          position: relative;
          &::after {
            width: 100px;
            left: 0;
            right: 0;
            @apply block bg-white absolute;
            margin: 0 auto;
            margin-top: 30px;
            top: 50%;
            left: 0;
            right: 0;
            transform: translateY(-50%);
            content: "";
            height: 1px;
          }
        }
        @screen lg {
          @apply border-0 border-l border-r;
          .block-text {
            padding-bottom: 200px;
            &::after {
              top: auto;
              bottom: 200px;
              left: 10%;
              right: 10%;
              transform: translateY(0);
              max-width: 100%;
              width: auto;
            }
          }
        }
      }

      video {
        position: absolute;
        top: 0;
        height: 100%;
        width: 100%;
        z-index: 1;
        left: 0;
        opacity: 0;
        object-fit: cover;
        transition: 0.3s opacity 0.5s ease-in-out;
      }
      .block-image {
        @apply flex absolute inset-0 opacity-0 overflow-hidden;
        transition: 0.3s all 0.3s ease-in-out;
        img {
          @apply h-full w-full object-cover;
          transform: scale(1);
          animation: scaleAnimate 60s infinite alternate;
          animation-play-state: paused;
        }
      }
      &:hover {
        // &::before {
        //   opacity: 1;
        // }
        video {
          opacity: 1;
        }
        .block-image {
          opacity: 1;
          img {
            animation-play-state: running;
          }
        }
        .block-text {
          @apply z-20;
        }
      }
    }
  }
}
