.c-number-counter {
  @apply inline-flex items-center border border-gray-300 px-4;
  padding-top: 2px;
  padding-bottom: 2px;
  button {
    @apply border-none px-10 py-4 bg-gray-100 cursor-pointer;
    width: 30px;
    &:focus,
    &:active {
      border: none;
      outline: none;
    }

    &:hover {
      @apply bg-gray-400 text-white;
    }
  }
  input {
    height: 40px;
    width: 50px;
    @apply px-10;
    &:focus {
      outline: none;
      box-shadow: none;
    }
  }
}
