@import "~slick-carousel/slick/slick";
@import "~lightbox2/dist/css/lightbox.css";

.slick-slider {
  user-select: auto;
  touch-action: pinch-zoom;
  touch-action: pan-y;
  -ms-touch-action: pan-y;
}

.lb-nav a.lb-next {
  background: url("../images/icon-arrow-right-white.svg") no-repeat !important;
  background-position: calc(100% - 25px) center !important;
  background-size: 25px !important;

  width: 50%;
}

.lb-nav a.lb-prev {
  background: url("../images/icon-arrow-left-white.svg") no-repeat !important;
  background-position: 25px center !important;
  background-size: 25px !important;
  padding-left: 25px;
  width: 50%;
}
.lb-data .lb-close {
  background: url("../images/clear.svg") no-repeat !important;
  background-position: center center !important;
  background-size: auto !important;
}
