.c-carousel {
  @apply pb-32;
  .arrow-right {
    border: 1px solid #000;
    width: 35px;
    height: 35px;
    @apply rounded-full flex items-center justify-center cursor-pointer p-6 mx-auto mt-10;
    svg {
      transform: rotate(90deg);
    }
    &:hover {
      @apply bg-black;
      svg {
        * {
          stroke: #fff;
        }
      }
    }

    @screen md {
      @apply absolute right-20;
      top: 50%;
      transform: translateY(-50%);
      svg {
        transform: rotate(0deg);
      }
    }
  }

  @screen md {
    @apply pr-56;
  }
}
