.c-form {
  .form-group {
    @apply mb-32;
  }
  .form-label {
    @apply uppercase text-sm font-light block text-gray-400 tracking-wider;
  }
  .form-control {
    @apply border-b border-gray-300 w-full rounded-none;
    height: 40px;
    &.error {
      @apply border-red-500;
    }
    &:focus {
      outline: none;
      box-shadow: none;
    }
  }

  .error {
    @apply text-red-500 text-sm;
  }
  textarea {
    &.form-control {
      height: 250px;
      @apply border border-gray-300 rounded-sm p-16;
    }

    &.sm {
      height: 180px;
    }
  }
}
