.c-table {
  min-width: 600px;
  &-header {
    .table-col {
      @apply text-xl text-left px-16;
    }
  }
  &-body {
    .table-col {
      @apply text-gray-400 py-16 px-16 border-b border-gray-300;
      vertical-align: top;
    }
    .c-table-row {
      &:nth-child(even) {
        @apply bg-gray-100;
      }
    }
  }
}
