.placeholder {
  position: relative;
  background: #ccc;
  width: 100%;
  height: auto;
  overflow: hidden;
}
.blur {
  filter: blur(25px);
  animation-name: example;
  animation-duration: 0.5s;
  animation-delay: 0.5s;
  animation-timing-function: ease-in-out;
  animation-fill-mode: forwards;
}

.no-blur {
  filter: blur(0);
  transition: filter 0.5s 2s ease-in;
}

.hd {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  opacity: 0;
  animation-name: example;
  animation-duration: 1s;
  animation-delay: 2s;
  animation-timing-function: ease-in-out;
  animation-fill-mode: forwards;
  object-fit: cover;
  image-rendering: crisp-edges;
  image-rendering: pixelated;
}

@keyframes example {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
