.page-category-detail {
  .slider-for {
    .arrow-right {
      svg {
        transform: rotate(0deg);
      }
      @screen md {
        @apply mt-0 right-0;
      }
    }
    @screen md {
      @apply pr-56;
    }
  }
  .slider-nav {
    @apply pr-40 pb-0;

    .arrow-right {
      @apply absolute right-6 p-6 mt-0;
      top: 50%;
      transform: translateY(-50%);
      width: 25px;
      height: 25px;
      svg {
        transform: rotate(0deg);
      }
    }
    @screen md {
      @apply pr-56;
    }
    @screen lg {
      .arrow-right {
        width: 35px;
        height: 35px;
      }
    }
  }
}
