}

.placeholder img {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  transition: opacity 1s linear;
}

.placeholder {
  position: relative;
  background: #ccc;
  width: 100%;
  height: auto;
  overflow: hidden;
}

.blur {
  filter: blur(25px);
  animation-name: example;
  animation-duration: 0.5s;
  animation-delay: 0.5s;
  animation-timing-function: ease-in-out;
  animation-fill-mode: forwards;
}

.no-blur {
  filter: blur(0);
  transition: filter 0.5s 2s ease-in;
}

.hd {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  opacity: 0;
  animation-name: example;
  animation-duration: 1s;
  animation-delay: 2s;
  animation-timing-function: ease-in-out;
  animation-fill-mode: forwards;
  object-fit: cover;
  image-rendering: crisp-edges;
  image-rendering: pixelated;
}

@keyframes example {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.c-banner {
  @apply relative;
}
.c-banner img {
  @apply object-cover;
}
.c-banner .placeholder {
  height: auto;
}
.c-banner .placeholder::before {
  content: "";
  @apply block absolute inset-0 bg-black opacity-50 z-10;
}
@screen lg {}
.c-banner.c-banner-home .placeholder {
  height: 100vh;
}
.c-banner .blocks .block {
  @apply relative tracking-widest;
  transition: all 0.4s;
}
.c-banner .blocks .block::before {
  content: "";
  @apply inset-0 absolute z-10;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
}
.c-banner .blocks .block .block-text {
  cursor: pointer;
  @apply text-white;
}
.c-banner .blocks .block .block-text:visited, .c-banner .blocks .block .block-text:active {
  color: #fff !important;
}
@screen lg {
  .c-banner .blocks .block .block-text {
    padding-bottom: 100px;
    @apply tracking-normal;
    z-index: 99;
    transition: all 0.3s linear;
  }
  .c-banner .blocks .block .block-text:hover {
    @apply tracking-wide;
  }
}
@screen lg {
  .c-banner .blocks .block:first-child .block-text {
    position: relative;
  }
  .c-banner .blocks .block:first-child .block-text::before {
    content: "";
    @apply block absolute left-0 bg-white;
    bottom: 150px;
    width: 20%;
    height: 1px;
    transition: all 0.3s ease-in-out;
  }
}
@screen lg {
  .c-banner .blocks .block:last-child .block-text {
    position: relative;
  }
  .c-banner .blocks .block:last-child .block-text::after {
    content: "";
    @apply block absolute right-0 bg-white;
    bottom: 150px;
    width: 20%;
    height: 1px;
    transition: all 0.3s ease-in-out;
  }
}
@screen lg {
  .c-banner .blocks .block:first-child:hover .block-text::before, .c-banner .blocks .block:first-child:hover .block-text::after, .c-banner .blocks .block:last-child:hover .block-text::before, .c-banner .blocks .block:last-child:hover .block-text::after {
    width: 25%;
  }
}
.c-banner .blocks .block:nth-child(2) {
  border-color: rgba(255, 255, 255, 0.3);
  @apply border-l-0 border-r-0;
}
.c-banner .blocks .block:nth-child(2) .block-text {
  position: relative;
}
.c-banner .blocks .block:nth-child(2) .block-text::after {
  width: 100px;
  left: 0;
  right: 0;
  @apply block bg-white absolute;
  margin: 0 auto;
  margin-top: 30px;
  top: 50%;
  left: 0;
  right: 0;
  transform: translateY(-50%);
  content: "";
  height: 1px;
}
@screen lg {
  .c-banner .blocks .block:nth-child(2) {
    @apply border-0 border-l border-r;
  }
  .c-banner .blocks .block:nth-child(2) .block-text {
    padding-bottom: 200px;
  }
  .c-banner .blocks .block:nth-child(2) .block-text::after {
    top: auto;
    bottom: 200px;
    left: 10%;
    right: 10%;
    transform: translateY(0);
    max-width: 100%;
    width: auto;
  }
}
.c-banner .blocks .block video {
  position: absolute;
  top: 0;
  height: 100%;
  width: 100%;
  z-index: 1;
  left: 0;
  opacity: 0;
  object-fit: cover;
  transition: 0.3s opacity 0.5s ease-in-out;
}
.c-banner .blocks .block .block-image {
  @apply flex absolute inset-0 opacity-0 overflow-hidden;
  transition: 0.3s all 0.3s ease-in-out;
}
.c-banner .blocks .block .block-image img {
  @apply h-full w-full object-cover;
  transform: scale(1);
  animation: scaleAnimate 60s infinite alternate;
  animation-play-state: paused;
}
.c-banner .blocks .block:hover video {
  opacity: 1;
}
.c-banner .blocks .block:hover .block-image {
  opacity: 1;
}
.c-banner .blocks .block:hover .block-image img {
  animation-play-state: running;
}
.c-banner .blocks .block:hover .block-text {
  @apply z-20;
}

